@font-face {
    font-family: "Google Sans";
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local(''),
    url('../assets/fonts/google-sans-regular.woff2') format("woff2");
}

@font-face {
    font-family: "Google Sans";
    font-style: normal;
    font-weight: 500;
    font-display: swap;
    src: local(''),
    url('../assets/fonts/google-sans-medium.woff2') format("woff2");
}

@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local('Roboto'),
    local('Roboto-Regular'),
    url('../assets/fonts/roboto-v30-latin-regular.woff2') format('woff2');
}

@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-display: swap;
    src: local('Roboto Medium'), local('Roboto-Medium'),
    url('../assets/fonts/roboto-v30-latin-500.woff2') format('woff2');
}

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    outline: none;
}

html {
    font-size: 14px;
    --primaryFont: "Roboto", sans-serif;
    --secondaryFont: "Google Sans", sans-serif;
    --taskcolor0: #ffffff;
    --primarylight2: #4BA0F4;
    --offwhite3: #70757a;
    --primary1: #1a6ee2;
    --primary2: #125ab8;
    --error: #d93025;
    --red1: #B01244;
    --red2: #7b0d25;
    --red3: #480816;
    --green1: #2FAD76;
    --borderradius: 4px;
    --overlay1: rgba(0, 0, 0, 0.2);
    --overlay2: rgba(0, 0, 0, 0.5);
    --box-shadow1: rgba(0, 0, 0, 0.14) 0px 8px 10px 1px,
    rgba(0, 0, 0, 0.12) 0px 3px 14px 2px,
    rgba(0, 0, 0, 0.2) 0px 2px 4px -1px;
    --box-shadow2: rgba(83, 88, 95, 0.14) 0px 4px 5px 0px,
    rgba(0, 0, 0, 0.2) 0px 1px 10px 0px,
    rgba(83, 88, 95, 0.1) 0px 1px 2px -1px;
    --box-shadow3: rgba(0, 0, 0, 0.19) 0 10px 20px,
    rgba(0, 0, 0, 0.23) 0 6px 6px;
    --box-shadow4: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    --box-shadow6: rgba(83, 88, 95, 0.14) 0px 1px 2px -1px,
    rgba(0, 0, 0, 0.2) 0px 1px 10px 0px,
    rgba(83, 88, 95, 0.1) 0px 4px 5px 0px;
    --box-shadow2-hover: rgba(60, 64, 67, 0.302) 0px 4px 4px 0px,
    rgba(60, 64, 67, 0.149) 0px 8px 10px 4px;
    --box-shadowbottom: rgba(0, 0, 0, 0.1) 3px 4px 5px 0px,
    rgba(0, 0, 0, 0.1) 3px 3px 6px 0px,
    rgba(0, 0, 0, 0.2) 3px 3px 4px -1px;
    --box-shadowinset1: rgba(0, 0, 0, 0.2) inset 3px 4px 5px 0px,
    rgba(0, 0, 0, 0.1) inset -1px -2px 3px 0px,
    rgba(0, 0, 0, 0.1) inset 3px 3px 6px 0px,
    rgba(0, 0, 0, 0.2) inset 3px 3px 4px -1px;
    --box-shadow-sbfixed: rgba(0, 0, 0, .3) 4px 6px 6px 0;
}

html[theme-mode='dark'] {
    --primary5: #1c1f21;
    --primarylight1: #4c9cf8;
    --black000: #0c0e0e;
    --black0: #141617;
    --black1: #181a1b;
    --black2: #1c1f21;
    --black3: #131516;
    --darkgrey000: #111314;
    --darkgrey0: #1f2325;
    --darkgrey1: #222527;
    --darkgrey2: #1f2223;
    --darkgrey3: #3c4043;
    --darkgrey4: #1f2223;
    --mediumgrey0: #26292b;
    --mediumgrey1: #393d41;
    --mediumgrey2: #484c50;
    --mediumgrey3: #95a0a5;
    --primary3: #122e5c;
    --primary4: #0f2c4f;
    --taskcolor: #fcfbf9;
    --taskcolor2: #C0BAB2;
    --white1: #E8E6E3;
    --white2: #C0BAB2;
    --white3: #9C9E9F;
    --white4: #BFC0C1;
    --offwhite1: #9e9689;
    --offwhite2: #c0bab2;
    --bordergrey: 1px solid var(--mediumgrey1);
    --borderdarkgrey: 1px solid var(--mediumgrey2);
    --scrollerThumbBackground: rgb(43, 47, 49);
    --scrollerThumbBorder: 4px solid var(--black1);
    --scrollerThumbBackgroundHover: #3b4042;
    --scrollerTrackBackground: var(--black1);
    --box-shadow5: rgba(0, 0, 0, 0.14) 0px 8px 4px 0px,
    rgba(0, 0, 0, 0.12) 0px 3px 6px 0px,
    rgba(0, 0, 0, 0.2) 0px 2px 4px 0px;
}

html[theme-mode='light'] {
    --primary5: #e9eff6;
    --black000: rgb(224, 229, 237);
    --black0: #E8F0FE;
    --black1: #ffffff;
    --black2: #E8F0FE;
    --black3: #D2E3FC;
    --darkgrey000: #9AA0A6;
    --darkgrey0: #a9b0b7;
    --darkgrey1: #9da4aa;
    --darkgrey2: #979ea5;
    --darkgrey3: #f1f3f4;
    --darkgrey4: #f1f3f4;
    --mediumgrey0: rgb(219, 221, 224);
    --mediumgrey1: #dadce0;
    --mediumgrey2: #dadce0;
    --mediumgrey3: #dadce0;
    --taskcolor: #ffffff;
    --taskcolor2: #e8f0fe;
    --white1: #3c4043;
    --white2: #3c4043;
    --white3: #3c4043;
    --white4: #3c4043;
    --offwhite1: #7e848b;
    --offwhite2: #3C4043;
    --bordergrey: 1px solid #dadce0;
    --borderdarkgrey: 1px solid #dadce0;
    --primary3: #5693e8;
    --primary4: #41638c;
    --primarylight1: #2e7ae5;
    --scrollerThumbBackground: #dadce0;
    --scrollerThumbBorder: 4px solid #ffffff;
    --scrollerThumbBackgroundHover: #bdc1c6;
    --scrollerTrackBackground: #ffffff;
    --box-shadow5: rgba(51, 54, 55, 0.14) 0px 4px 4px 0px,
    rgba(51, 54, 55, 0.12) 0px 3px 2px 0px,
    rgba(51, 54, 55, 0.2) 0px 2px 3px 0px;
}

select,
button {
    border: none;
}

form > button {
    padding: 0.6rem 1.4rem;
}

input[type="search"],
input[type="date"],
input[type="time"],
input[type="checkbox"] {
    cursor: pointer;
}

select {
    padding: 0.55rem 0;
}

svg {
    user-select: none !important;
    pointer-events: none !important;
}

button:focus-visible {
    outline: 2px solid var(--primary1);
    background: var(--black0);
}

::selection {
    background: var(--primary1) !important;
    color: var(--taskcolor0) !important;
}

::-webkit-scrollbar {
    width: 20px;
    height: 100%;
}

::-webkit-scrollbar-thumb {
    background: var(--scrollerThumbBackground);
    border: var(--scrollerThumbBorder);
    border-radius: 20px;
}

::-webkit-scrollbar-thumb:hover {
    background: var(--scrollerThumbBackgroundHover);
}

::-webkit-scrollbar-track {
    background: var(--scrollerTrackBackground);
}